    See the JupyterLab Developer Guide for useful CSS Patterns:

    https://jupyterlab.readthedocs.io/en/stable/developer/css.html
*/

.jp-EscrowAI-progress {
  padding: 20px;
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  height: 100%;
  display: flex;
  flex-direction: column;
}

.escrowai-progress-container {
  max-width: 800px;
  margin: 0 auto;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.escrowai-header-container {
  position: sticky;
  top: 0;
  background: white;
  z-index: 10;
  padding-bottom: 10px;
  margin-bottom: 10px;
  border-bottom: 1px solid #eee;
}

.escrowai-step-label {
  font-size: 18px;
  font-weight: bold;
  color: #2c3e50;
  margin-bottom: 10px;
  padding: 10px;
  background: #f8f9fa;
  border-radius: 4px;
  border-left: 4px solid #4CAF50;
  transition: border-color 0.3s ease;
}

.escrowai-step-label.error {
  border-left-color: #dc3545;
}

.escrowai-progress-bar {
  height: 8px;
  background: #eee;
  border-radius: 4px;
  overflow: hidden;
  margin: 15px 0;
}

.escrowai-progress-fill {
  height: 100%;
  background: #4CAF50;
  width: 0;
  transition: width 0.3s ease, background-color 0.3s ease;
}

.escrowai-progress-fill.error {
  background: #dc3545;
}

.escrowai-details-label {
  font-family: monospace;
  font-size: 13px;
  color: #24292e;
  margin-top: 10px;
  padding: 15px;
  background: #f6f8fa;
  border-radius: 6px;
  border: 1px solid #e1e4e8;
  overflow-y: auto;
  flex-grow: 1;
  max-height: calc(100vh - 200px);
  white-space: pre-wrap;
  word-break: break-word;
}

.escrowai-step-content {
  margin-bottom: 15px;
  border: 1px solid #e1e4e8;
  border-radius: 4px;
  overflow: hidden;
}

.escrowai-step-header {
  font-weight: bold;
  padding: 8px 12px;
  background: #f0f0f0;
  border-bottom: 1px solid #e1e4e8;
}

.escrowai-output-line {
  padding: 8px;
  margin: 4px 0;
  background: #ffffff;
  border: 1px solid #e1e4e8;
  border-radius: 4px;
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', 'Consolas', monospace;
  line-height: 1.5;
}

.escrowai-output-line:hover {
  background: #f8f9fa;
  border-color: #d0d7de;
}

.escrowai-output-line br {
  line-height: 1.6;
}
